import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  AppBar,
  Container,
  Divider,
  Grid,
  SwipeableDrawer,
  Typography,
} from "@material-ui/core";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { Box, ThemeProvider, useScrollTrigger } from "@material-ui/core";

import { createMuiTheme } from "@material-ui/core/styles";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import Slide from "@material-ui/core/Slide";
import Avatar from "@material-ui/core/Avatar";
import CloseIcon from "@material-ui/icons/Close";

import avatar from "../images/avatar-1.jpg";

const theme = createMuiTheme({
  shadows: ["none"],
  palette: {
    primary: {
      main: "#1DA1F2",
    },
    secondary: {
      main: "#14171A",
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  toolbar: {
    justify: "space-between",
    borderBottom: "1px solid #c1c1c1",
  },
  menuButton: {
    marginRight: theme.spacing(1),
    padding: theme.spacing(0),
  },
  title: {
    flexGrow: 1,
  },
  twLogo: {
    height: 22,
  },
  list: {
    width: 275,
  },
}));

function HideOnScroll(props) {
  const { children } = props;

  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

function Header(props) {
  const classes = useStyles();

  const [state, setState] = React.useState({
    drawerOpen: false,
  });

  const toggleDrawer = (open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, drawerOpen: open });
  };

  const list = () => {
    return (
      <div
        className={classes.list}
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <Container maxWidth="xl">
          <Grid
            container
            spacing={1}
            alignItems="center"
            justify="space-between"
          >
            <Grid>
              <Typography variant="subtitle1" component="div">
                <Box component="p" mb={1} fontWeight="fontWeightBold">
                  Account info
                </Box>
              </Typography>
            </Grid>
            <Grid>
              <IconButton color="primary" size="small">
                <CloseIcon />
              </IconButton>
            </Grid>
          </Grid>
        </Container>
        <Divider />

        <Container maxWidth="xl">
          <Box mt={1}>
            <Box>
              <Avatar alt="Chamberlain Francis" src={avatar} />
            </Box>
            <Typography component="div">
              <Box component="p" mt={1} mb={-0.5} fontWeight="fontWeightBold">
                Chamberlain
              </Box>
              <Box component="span" m={0}>
                @freesou_l
              </Box>
            </Typography>
          </Box>
        </Container>

        <Container maxWidth="xl">
          <Box display="flex" mt={2}>
            <Typography component="div">
              <Box component="span" mr={3}>
                123 following
              </Box>
            </Typography>
            <Typography component="div">
              <Box component="span">110 followers</Box>
            </Typography>
          </Box>
        </Container>

        {/* <List>
          {["Inbox", "Starred", "Send email", "Drafts"].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <Divider /> */}
      </div>
    );
  };

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <HideOnScroll {...props}>
          <AppBar position="fixed" color="inherit" elevation={0}>
            <Toolbar variant="dense" className={classes.toolbar}>
              <React.Fragment key={state.drawerOpen}>
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={toggleDrawer(true)}
                >
                  <Avatar
                    alt="Chamberlain Francis"
                    src={avatar}
                    style={{ height: 35, width: 35 }}
                  />
                </IconButton>

                <SwipeableDrawer
                  anchor="left"
                  open={state.drawerOpen}
                  onClose={toggleDrawer(false)}
                  onOpen={toggleDrawer(true)}
                  elevation={0}
                >
                  {list()}
                </SwipeableDrawer>
              </React.Fragment>

              <Typography
                variant="h6"
                color="inherit"
                className={classes.title}
              >
                <Box fontWeight="fontWeightBold">Home</Box>
              </Typography>

              <Box>
                <IconButton color="inherit">
                  <MoreVertIcon color="primary" />
                </IconButton>
              </Box>
            </Toolbar>
          </AppBar>
        </HideOnScroll>
      </div>
    </ThemeProvider>
  );
}

export default Header;
